.scoreTile {
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0.5rem;
    height: 4.5rem;
    width: 7.5rem;
}

.scoreText {
    font-weight: 600;
    text-align: center;
    font-size: 0.9rem;
    margin: 0;
    color: hsl(229, 64%, 46%);
}

.score {
    font-weight: 700;
    font-size: 2.5rem;
    margin: 0;
    color: hsl(229, 25%, 31%);
}

.prevScore {
    opacity: 0;
    font-weight: 700;
    font-size: 2.5rem;
    margin: 0;
    color: hsl(229, 25%, 31%);
    position: absolute;
    top: 53px;
}

@media(max-width: 500px) {
    .prevScore {
        top: 43px;
    }
}

.scoreAnimation {
    animation-name: scoreFadeIn;
    animation-duration: 3s;
    animation-fill-mode: forwards;
}

.prevScoreAnimation {
    animation-name: prevScoreFadeOut;
    animation-duration: 3s;
    animation-fill-mode: forwards;
}

@keyframes scoreFadeIn {
    0% {
        opacity: 0;
    }

    80% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes prevScoreFadeOut {
    0% {
        opacity: 1;
    }

    80% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}